	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Including Britain","date":"19 Jun 2005","slug":"/2005-06-19-including-britain/","tags":"Y2005"},"html":"<p><a href=\"http://www.economist.com/\"><img alt=\"including_gb.png\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2005/06/including_gb.png\" width=\"175\" height=\"88\" align=\"left\" border=\"0\" /></a> OK, it's an <a href=\"http://www.fpri.org/enotes/20030312.europe.radu.oldeuropevsnew.html\" title=\"Dense fog over English Channel. Continent isolated.\">old joke</a>, but given the <a href=\"http://www.economist.com/agenda/displayStory.cfm?story_id=4097374\" title=\"Europe's identity crisis deepens | Economist.com\">collapse of the EU budget talks</a> over the British rebate, it's more appropriate than ever that <a href=\"http://www.economist.com/\">The Economist</a> web site explicitly states that Europe includes Britain.</p>"},"next":{"fields":{"title":"Your Blog and Your Career","date":"18 Jun 2005","slug":"/2005-06-18-your-blog-and-your-career/"}},"previous":{"fields":{"title":"Am I Next?","date":"20 Jun 2005","slug":"/2005-06-20-am-i-next/"}}},{"node":{"fields":{"title":"Your Blog and Your Career","date":"18 Jun 2005","slug":"/2005-06-18-your-blog-and-your-career/","tags":"Y2005"},"html":"<p><a href=\"http://www.welt.de/\">Die Welt</a> is a year late and a few Euros short. Just today they report that <a href=\"http://www.welt.de/data/2005/06/18/733179.html\" title=\"Um Kopf und Kragen geplaudert\">revealing details about work in your blog</a> can cost you your job. Doh! Revealing them by cellphone or e-mail can be equally dangerous, better stay away from them too! Fortunately my boss <a href=\"http://lumma.de/eintrag.php?id=1767\" title=\"Blog killt Karriere? [Lummaland - das Weblog]\">Nico doesn't feel that way</a>. We don't have a formal <a href=\"http://www.papascott.de/archives/2005/02/11/blogging-policy/\" title=\"PapaScott: Blogging Policy\">blogging policy</a>, but as long as I don't openly criticize his <a href=\"http://lumma.de/eintrag.php?id=1769\" title=\"Doenerstag [Lummaland - das Weblog]\">D&ouml;ner pictures</a>, I think I'm OK.</p>"},"next":{"fields":{"title":"Blackberry","date":"16 Jun 2005","slug":"/2005-06-16-blackberry/"}},"previous":{"fields":{"title":"Including Britain","date":"19 Jun 2005","slug":"/2005-06-19-including-britain/"}}},{"node":{"fields":{"title":"Blackberry","date":"16 Jun 2005","slug":"/2005-06-16-blackberry/","tags":"Y2005"},"html":"<p><img alt=\"crackberry.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2005/06/crackberry.jpg\" width=\"100\" height=\"133\" align=\"right\" /> I am important. I now have a <a href=\"http://www.blackberry.com/\">Blackberry</a> <a href=\"http://www.blackberry.net/ap/products/handhelds/blackberry7230.shtml\">7230</a>.</p>\n<p>Actually, it's a hand-me-down, so I'm actually not all that important. (No, it is not Nico's famous <a href=\"http://lumma.de/eintrag.php?id=1684\">Blackberry with wings</a>.) </p>\n<p>At least I now have cell reception at home (Vodafone instead of E-Plus), which is convenient. This is the \"No Messages\" screen. My favorite command is \"Delete Prior\" which, if done correctly, actually deletes all.</p>"},"next":{"fields":{"title":"Terminal Panic","date":"14 Jun 2005","slug":"/2005-06-14-terminal-panic/"}},"previous":{"fields":{"title":"Your Blog and Your Career","date":"18 Jun 2005","slug":"/2005-06-18-your-blog-and-your-career/"}}}],"pathPrefix":"","first":false,"last":false,"index":303,"pageCount":1101}}

